import React from 'react';
import './about.css';

export default function About() {
  return (
    <div className='container'>
      <h2 className='title'>About</h2>
      <p className='about-content'>
        I am a web developer and programmer from Bangalore, India, who is
        passionate about programming and building web apps. I hold a Bachelor's
        Degree in Computer Applications. I have 2+ years of experience in
        designing and developing efficient web apps, and I am currently working
        at{' '}
        <a
          href='https://www.happiestminds.com/'
          target='_blank'
          rel='noopener noreferrer'
        >
          Happiest Minds Technologies
        </a>
        , Bangalore. I code mostly using JavaScript, TypeScript, C#, Python and
        PHP, and I use frameworks such as React, Angular, .Net core and Express.
        I am familiar with SQL and NoSQL databases. In addition to my knowledge
        base, I actively seek out new technologies and stay up-to-date on
        industry trends and advancements. This has allowed me to stay ahead of
        the curve and deliver exceptional work to all of my employers.
      </p>
    </div>
  );
}
