import React from 'react';
import './social-links.css';

import github_logo from '../../assets/github.png';
import twitter_logo from '../../assets/twitter.png';
import linkedin_logo from '../../assets/linked_in.png';

export default function SocialLinks() {
    return (
        <div className='social-links'>
          <a href='https://github.com/beerendramc' target='_blank' rel='noopener noreferrer'>
            <img src={github_logo} alt='github_icon' />
          </a>
          <a href='https://twitter.com/beerendra_mc' target='_blank' rel='noopener noreferrer'>
            <img src={twitter_logo} alt='github_icon' />
          </a>
          <a href='https://www.linkedin.com/in/beerendra-m-c-80854b149' target='_blank' rel='noopener noreferrer'>
            <img src={linkedin_logo} alt='github_icon' />
          </a>
        </div>
        );
  }
