import React from 'react';
import './work.css';

import nextIcon from '../../assets/round_navigate_next_white_18dp.png';
import prevIcon from '../../assets/round_navigate_before_white_18dp.png';

export default function Work() {
  const [slideIndex, setSlideIndex] = React.useState(1);

  React.useEffect(() => {
    showSlides(slideIndex);
  });

  function showSlides(n) {
    console.log(n);
    let slides = document.getElementsByClassName('slides');
    if (n > slides.length) {
      setSlideIndex(1);
    }
    if (n < 1) {
      setSlideIndex(slides.length);
    }
    for (let i = 0; i < slides.length; i++) {
      slides[i].style.display = 'none';
    }
    slides[slideIndex - 1].style.display = 'block';
  }

  function plusSlides(n) {
    setSlideIndex(state => state + n);
    showSlides(slideIndex + n);
  }

  function onKeyUp(e, n) {
    if (e.key === 'Enter') {
      plusSlides(n);
    }
  }

  return (
    <div className='container'>
      <h2 className='title'>
        Work <span className='description'>(Personal)</span>
      </h2>
      <div className='work-content'>
        <div className='slideshow-container'>
          <div className='slides'>
            <h3>Generic E-commerce web api</h3>
            <ul>
              <li>A generic e-commerce based RESTful api.</li>
              <li>Built on Nodejs (Express) and MongoDB Atlas.</li>
              <li>
                Integrated with swagger-UI, endpoints are self explanatory.
              </li>
            </ul>
            <div>
              <a
                href='https://github.com/BeerendraMC/Nodejs-API'
                target='_blank'
                rel='noopener noreferrer'
              >
                Source code
              </a>
              <a
                href='https://scotch-hub-api.herokuapp.com/api-docs'
                target='_blank'
                rel='noopener noreferrer'
              >
                Hosted app
              </a>
            </div>
          </div>
          <div className='slides'>
            <h3>Scotch-hub</h3>
            <ul>
              <li>An e-commerce web application.</li>
              <li>Built on PHP, jQuery and Bootstrap.</li>
            </ul>
            <div>
              <a
                href='https://bitbucket.org/beerendramc/scotch-hub'
                target='_blank'
                rel='noopener noreferrer'
              >
                Source code
              </a>
              <a
                href='http://scotchhubb.000webhostapp.com/'
                target='_blank'
                rel='noopener noreferrer'
              >
                Hosted app
              </a>
            </div>
          </div>
          <div className='slides'>
            <h3>Custom Angular Material Grid</h3>
            <ul>
              <li>A configurable and re-usable grid component.</li>
              <li>Built on angular 8 using Angular Material data table.</li>
              <li>
                Along with searching, sorting and pagination it supports
                Hyperlink, Dropdown, Name-description columns and other
                features.
              </li>
            </ul>
            <div>
              <a
                href='https://github.com/BeerendraMC/CustomAngularMaterialGrid'
                target='_blank'
                rel='noopener noreferrer'
              >
                Source code
              </a>
              <a
                href='https://github.com/BeerendraMC/CustomAngularMaterialGrid#documentation'
                target='_blank'
                rel='noopener noreferrer'
              >
                Documentation
              </a>
            </div>
          </div>
          <div className='slides last-slide'>
            <h3>Portfolio and CRUDs</h3>
            <ul>
              <li>This portfolio site is built on React.</li>
              <li>Also built Angular, React CRUDs and other projects.</li>
              <li>
                Visit my{' '}
                <a
                  href='https://github.com/BeerendraMC'
                  target='_blank'
                  rel='noopener noreferrer'
                >
                  GitHub account
                </a>{' '}
                for more information.
              </li>
            </ul>
          </div>
          <span
            className='prev'
            tabIndex='0'
            onClick={() => plusSlides(-1)}
            onKeyPress={e => onKeyUp(e, -1)}
          >
            <img src={prevIcon} alt='navigate_prev_icon' />
          </span>
          <span
            className='next'
            tabIndex='0'
            onClick={() => plusSlides(1)}
            onKeyPress={e => onKeyUp(e, 1)}
          >
            <img src={nextIcon} alt='navigate_next_icon' />
          </span>
        </div>
      </div>
    </div>
  );
}
